<template>
  <div class="lottery">
    <img class="wheel" src="@/assets/images/lottery_img.png" />
    <img
      class="pointer"
      :style="{'transform':styleTransform,'transition':'transform 6s ease-in-out'}"
      src="@/assets/images/pointer.png"
      @click="startLottery"
    />
    
  </div>
</template>
<script>
export default {
  data() {
    return {
      styleTransform: "rotate(0deg)", //初始旋转角度\
      isDisabled: false,
      rotateStartAngle: 0
    };
  },
  mounted() {},
  methods: {
    startLottery() {
      if (!this.isDisabled) {
        this.getLottery();
      }
    },
    getLottery() {
      this.isDisabled = true;
      let fullCircleCount = 10 * 360; //转几圈
      let resultIndex = this.randomResult(); //得奖结果
      //初始旋转角度 每次抽奖重新注册
      let rotateAngle =
        this.rotateStartAngle +
        fullCircleCount +
        30 * resultIndex -
        (this.rotateStartAngle % 360); //设置当前旋转的角度  转圈数 + 当前结果的角度 - 上次旋转的角度
      this.styleTransform = "rotate(" + rotateAngle + "deg)"; //给指针赋值 当前旋转的角度
      this.rotateStartAngle = rotateAngle; // 设置下次旋转的起始度数
      setTimeout(() => {
        this.endLottery();
      }, 6500);
    },
    endLottery() {
      this.$eventBus.$emit("showRegister");
    },
    randomResult() {
      let arr = [11, 2, 4, 9];
      let index = Math.floor(Math.random() * arr.length);
      return arr[index];
    }
  }
};
</script>
<style scoped lang="stylus">
.lottery {
  width: 100%;

  .wheel {
    display: block;
    margin: 0 auto;
    width: 90%;
  }

  .pointer {
    position: absolute;
    left: 31%;
    top: 20%;
    width: 37%;
    transform-origin: 48% 60%;
  }
}
</style>

